import { FC } from "react";
import classNames from "classnames/bind";

import { Heading, HeadingTag } from "common/components/new/Heading";
import { Picture } from "common/components/Picture";

import { AccordionList } from "./components/AccordionList";
import { QuestionsProps } from "./Questions.types";
import styles from "./Questions.module.scss";

const cx = classNames.bind(styles);

const media = "(min-width: 1024px)";

export const Questions: FC<QuestionsProps> = (props) => {
    const {
        title,
        list,
        description,
        isSite,
        oneColumn,
        className,
        withImages,
        firstImage,
        secondImage,
        classNameTitle,
        expanded,
    } = props;

    return (
        <section
            className={cx("root", className, {
                "root_with-images": withImages,
            })}
        >
            <div
                className={cx("row", oneColumn && "row_block", {
                    "row_with-images": withImages,
                })}
            >
                <div
                    className={cx(
                        "column",
                        withImages
                            ? "column-title-max-width"
                            : "column-without-image"
                    )}
                >
                    <Heading
                        tag={HeadingTag.H2}
                        className={cx(
                            "title",
                            classNameTitle,
                            isSite && "title_site",
                            {
                                title_withMargin: description,
                            }
                        )}
                    >
                        {title}
                    </Heading>
                    <p className={cx("description")}>{description}</p>
                    {withImages && firstImage && (
                        <div className={cx("container-first-image")}>
                            <Picture
                                classNameImg={cx("image")}
                                desktop={{
                                    webp: firstImage.desktop?.webp,
                                    jpeg: firstImage.desktop?.jpeg,
                                    media,
                                }}
                            />
                            <div className={cx("wave-background")} />
                        </div>
                    )}
                    {withImages && secondImage && (
                        <div className={cx("container-second-image")}>
                            <Picture
                                classNameImg={cx("image")}
                                desktop={{
                                    webp: secondImage.desktop?.webp,
                                    jpeg: secondImage.desktop?.jpeg,
                                    media,
                                }}
                            />
                        </div>
                    )}
                </div>
                <div
                    className={cx(
                        "column",
                        withImages
                            ? "column-text-max-width"
                            : "column-without-image"
                    )}
                >
                    <AccordionList questions={list} expanded={expanded} />
                </div>
            </div>
        </section>
    );
};
