import { FunctionComponent, useCallback, useRef } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Icon } from "common/components/Icons";
import CrossIcon from "common/components/Icons/icons/24/cross.svg";

import { AccordionItemProps } from "./AccordionItem.types";
import styles from "./AccordionItem.module.scss";

const cx = classNames.bind(styles);

const AccordionItem: FunctionComponent<AccordionItemProps> = ({
    index,
    content,
    active = "",
    onOpen,
    expanded,
}) => {
    const { answer, question } = content;

    const activeItem = useRef<HTMLDivElement>(null);
    const height =
        (active &&
            activeItem &&
            activeItem.current &&
            activeItem.current.clientHeight) ||
        0;

    const handleAccordionClick = useCallback(() => {
        onOpen(index);
    }, [onOpen, index]);

    return (
        <div className={cx("root")}>
            <div
                className={cx("question", {
                    clickable: !expanded,
                })}
                onClick={expanded ? undefined : handleAccordionClick}
            >
                <Heading
                    tag={HeadingTag.H3}
                    type={HeadingType.S}
                    className={cx("title", {
                        title_open: active || expanded,
                    })}
                >
                    <span dangerouslySetInnerHTML={{ __html: question }} />
                </Heading>
                {!expanded && (
                    <Icon
                        className={cx("icon", {
                            "icon--active": active,
                        })}
                    >
                        <CrossIcon />
                    </Icon>
                )}
            </div>
            <div
                className={cx("answer")}
                style={{ height: expanded ? "auto " : height }}
            >
                <div
                    className={cx("inner", {
                        inner_open: active || expanded,
                    })}
                    ref={activeItem}
                >
                    <p
                        className={cx("answer__text")}
                        dangerouslySetInnerHTML={{ __html: answer }}
                    />
                </div>
            </div>
        </div>
    );
};

export default AccordionItem;
